.menu {
  background-color: #e8eff9;
  max-width: 1260px;
  margin: 0 auto;

  &__content {
    padding: $header-top-padding-m 0;
    height: 80vh;


    @include onTablet {
      padding: $header-top-padding-td 0;
      height: 800px;
    }

    @include onDesktop {
      padding: $header-top-padding-td 0;
      height: 720px;
    }
  }

  &__top {
    margin-bottom: 55px;

    @include onTablet {
      margin-bottom: 56px;
    }

    @include onDesktop {
      margin-bottom: 80px;
    }
  }

  &__list {
    list-style: none;
    margin: 0 0 40px;
    padding: 0;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }

  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;
    color: #292929;
    text-decoration: none;
    font-weight: 700;
    font-size: 22px;
    letter-spacing: 2px;
    text-transform: uppercase;

    transition: border-color 0.3s;

    &:hover {
      border-color: #292929;
    }
  }

  &__call {
    display: block;
    width: max-content;

    font-weight: 700;
    font-size: 16px;

    align-items: center;
    text-transform: uppercase;
    text-decoration: none;
    color: #292929;

    &--phone {
      margin-bottom: 16px;
    }
    &--action {
      padding-bottom: 8px;
      border-bottom: 1px solid #292929;
;
      font-size: 12px;
      letter-spacing: 2px;
    }
  }
}
